.daily-question {
  position: relative;
  text-align: center;
  margin-top: 16px;
  margin-bottom: 16px;
}

.img-canvas {
  width: 190px;
  height: 280px;
  box-shadow: hsl(0deg 0% 0% / 10%) 1px 0px 20px 7px,
    hsl(0deg 0% 0% / 10%) 0px 30px 60px -30px;
}

.resolution-marker {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 5px;
  background-color: rgb(229, 9, 20);
  text-align: center;
  font-size: 12px;
  font-weight: bold;
}

@media only screen and (max-device-width: 375px) {
  .img-canvas {
    width: 150px;
    height: 220px;
  }

  .resolution-marker {
    font-size: 8px;
  }
}

@media only screen and (min-device-width: 376px) and (max-device-width: 420px) {
  .img-canvas {
    width: 150px;
    height: 220px;
  }

  .resolution-marker {
    font-size: 10px;
  }
}
